.filmsList {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-column-gap: 20px;
  grid-row-gap: 1em;
  list-style: none;
  padding: 30px;
  margin: 0;
}

.filmListItem {
  width: auto;
  cursor: pointer;
  border-radius: 3px;
  box-shadow: 0px 0px 7px 1px rgba(34, 60, 80, 0.19);
}

.filmListItemImg {
  object-fit: cover;
  width: 100%;
  border-radius: 3px;
}

.filmTitle {
  text-align: center;
  margin: 13px 7px;
}
